if(IG$.cVis.treemap) IG$.cVis.treemap.prototype.draw_protovis = function(mresult) {
	var me = this,
		i,
		j,
		data = mresult._tabledata,
		fc = mresult.colfix,
		fr = mresult.rowfix,
		mcr = me.sheetoption.model.chart_option,
		tm_l_cl = mcr.tm_l_cl,
		cols = (data.length > 0 ? data[0].length : 0),
		points = [],
		colmap = [],
		row,
		headers = [],
		h,
		pt,
		ppt,
		copt = {},
		cdoc,
		dval,
		container = $(me.container),
		treemap = {
			ty: 0
		},
		colors,
		ccnt = 0,
		cpath,
		ppoint,
		lpoint,
		crowpoints,
		cval,
		c_cset = mcr.colorset || mresult.c_cset,
		sep = (mcr ? mcr.xaxissep : null) || ig$.chart_separator || IG$._separator;
		
	sep = sep == "nl" ? "\n" : sep;

	container.empty();
	
	if (IG$.__chartoption && IG$.__chartoption.chartcolors && IG$.__chartoption.chartcolors[c_cset])
	{
		colors =  IG$.__chartoption.chartcolors[c_cset];
	}

	for (i=0; i < fc; i++)
	{
		colmap.push({
			value: null,
			seq: 0,
			point: null
		});
	}

	for (i=fc; i < cols; i++)
	{
		h = "";
		for (j=0; j < fr; j++)
		{
			h = (j == 0) ? data[j][i].text : h + sep + data[j][i].text;
		}
		headers.push({
			name: h,
			selected: (i - fc == me.ti ? true : false)
		});
	}

	if (headers && headers.length > 1)
	{
		cdoc = $("<div class='mtreemap-caption'></div>");
		container.append(cdoc);
		// for (i=0; i < headers.length; i++)
		$.each(headers, function(i, dobj) {
			$("<input type='radio' name='category' value='" + i + "'>" + dobj.name + "&nbsp;&nbsp;&nbsp;&nbsp;</input>")
				.prop("checked", dobj.selected)
				.appendTo(cdoc)
				.bind("change", function(ev) {
					me.ti = i;
					me.draw(mresult);
				});
		});

		treemap.ty = cdoc.height();
	}

	crowpoints = [];
		
	for (j=0; j < fc; j++)
	{
		crowpoints.push({});
	}

	for (i=fr; i < data.length; i++)
	{
		row = data[i];
		
		cpath = "";
		
		for (j=0; j < fc; j++)
		{
			val = row[j].text || row[j].code;

			if (j == 0)
			{
				if (!lpoint || (lpoint && lpoint.name != val))
				{
					pt = {
						name: val,
						path: val
					};
					
					points.push(pt);
					lpoint = pt;
				}
				else
				{
					pt = lpoint;
				}
				
				crowpoints[0] = pt;
			}
			else
			{
				ppoint = crowpoints[j-1];
				
				ppoint.children = ppoint.children || [];
				ppoint.childmap = ppoint.childmap || {};
				
				if (ppoint.childmap[val])
				{
					pt = ppoint.childmap[val];
				}
				else
				{
					pt = {
						name: val,
						path: ppoint.path + "/" + val
					};
					
					ppoint.children.push(pt);
					ppoint.childmap[val] = pt;
				}
				
				crowpoints[j] = pt;
			}
			
			ppoint = pt;
		}

		if (row.length > me.ti + fc)
		{
			dval = row[me.ti + fc];
			
			cval = Number(dval.code);
			
			if (isNaN(cval) == false)
			{
				for (j=0; j < crowpoints.length; j++)
				{
					pt = crowpoints[j];
					
					if (isNaN(pt.value))
					{
						pt.value = cval;
					}
					else
					{
						pt.value += cval;
					}
				}
			}
		}
	}

	var w = IG$.j$ext._w(container),
		h = IG$.j$ext._h(container),
		coption = {};

	me.drawmode = "treemap";

	treemap.box = $("<div></div>").appendTo(container);
	treemap.box.css({
		position: "absolute",
		width: w,
		height: h - treemap.ty,
		top: treemap.ty,
		left: 0
	});
	
	coption.grid = {
    	left: 10,
    	top: 10,
    	bottom: 10,
    	right: 10,
    	containLabel: true
    };
    
	coption.chart = {
		animation: false,
		renderTo: treemap.box[0],
		reflow: false,
		alignTicks: true,
		borderWidth: 0
	};

	coption.tooltip = {
		formatter: function (info) {
			var value = info.value;
			var treePathInfo = info.treePathInfo;
			var treePath = [];

			for (var i = 1; i < treePathInfo.length; i++) {
				treePath.push(treePathInfo[i].name);
			}

			return [
				'<div class="tooltip-title">' + treePath.join('/') + '</div>',
				': ' + value + '',
			].join("");
		}
	};
	
	coption.color = colors;
	
	var tvalue = mcr.title || "",
		ctitle,
		titleposition = mcr.titleposition || "TOP_CENTER",
		cgrid = coption.grid,
		measure_total = 0;

	if (mcr.showtitle == true && tvalue)
	{
		ctitle = coption.title = {
			text: tvalue,
			show: true,
					
			textStyle: {
				align: "center"
			}
		};
		
		if (titleposition.indexOf("BOTTOM_") > -1)
		{
			ctitle.bottom = 25;
			cgrid.bottom += 10;
		}
		else
		{
			cgrid.top += 30;
		}

		if (titleposition.indexOf("_LEFT") > -1)
		{
			ctitle.textAlign = "left";
			ctitle.textStyle.align = "left";
			ctitle.left = 20;
		}
		else if (titleposition.indexOf("_RIGHT") > -1)
		{
			ctitle.textAlign = "left";
			ctitle.right = 120;
			ctitle.textStyle.align = "right";
		}
		else
		{
			ctitle.textAlign = "center";
			ctitle.right = "50%";
			ctitle.left = "50%";
		}
	}
	else
	{
		coption.title = {text: "", show: false};
	}
	
	if (tm_l_cl)
	{
		coption.colorAxis = {
			minColor: "#FFFFFF",
			maxColor: colors[0]
		};
	}
	
	coption.series = [
		{
			type: "treemap",
			layoutAlgorithm: mcr.tm_l_alg || "squarified",
			allowDrillToNode: false,
			dataLabels: {
				enabled: false
			},
			label: {
				show: true,
				formatter: function (params) {//"{b}"	
					let treePathInfo = params.treePathInfo,
						pcts = treePathInfo && treePathInfo.length ? (treePathInfo[treePathInfo.length - 1].value * 100 / treePathInfo[0].value) : 0,
						arr = [
							"{name|" + params.name + "}",
							"{hr|}",
							// "{percent|" + pcts.toFixed(2) + "} {label|percent}",
							"{value|" + echarts.format.addCommas(params.value) + "} {label|" + pcts.toFixed(2) + "%}"
						];
					return arr.join("\n");
				},
				rich: {
					percent: {
						fontSize: 10,
						lineHeight: 30,
						color: "yellow"
					},
					label: {
						fontSize: 9,
						backgroundColor: 'rgba(0,0,0,0.3)',
						color: '#fff',
						borderRadius: 2,
						padding: [2, 4],
						lineHeight: 25,
						align: 'right'
					},
					name: {
						fontSize: 12,
						color: "#fff"
					},
					hr: {
						width: "100%",
						borderColor: "rgba(255,255,255,0.2)",
						borderWidth: 0.5,
						height: 0,
						lineHeight: 10
					}
				}
			},
			data: points,
			breadcrumb: {
				show: false
			},
			nodeClick: false
		}
	];

	if (fc > 1)
	{
		coption.series[0].allowDrillToNode = true;
		coption.series[0].levelIsConstant = false;
		coption.series[0].levels = [
			{
				itemStyle: {
					normal: {
						borderWidth: 2,
						gapWidth: 4
					}
				}
			},
			{
				colorSaturation: [0.3, 0.6],
				itemStyle: {
					normal: {
						borderColorSaturation: 0.7,
						gapWidth: 2,
						borderWidth: 2
					}
				}
			},
			{
				colorSaturation: [0.3, 0.5],
				itemStyle: {
					normal: {
						borderColorSaturation: 0.6,
						gapWidth: 1
					}
				}
			},
			{
				colorSaturation: [0.3, 0.5]
			}
		];
		coption.series[0].drillUpButton = {
			text: "< " + IRm$.r1("B_BACK"),
			relativeTo: "plotBox",
			position: {
				y: 0,
				x: 0
			}
		};
		coption.series[0].breadcrumb.show = true;
		coption.series[0].nodeClick = "zoomToNode";
	}

	treemap.map = echarts.init(treemap.box[0], mcr.echart_theme || ig$.echarts_theme || "amplix");
	treemap.map.setOption(coption);

	me.treemap = treemap;
}
// end of echart treemap
// }
